import { Component, OnInit } from '@angular/core';
import { ContactService } from 'src/app/services/contact.service';
import { JwtService } from 'src/app/services/jwt.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.sass']
})
export class ContactComponent implements OnInit {

  public contact;
  public user;

  constructor(private jwtService: JwtService, private contactService: ContactService) {
    this.contact = {}
    if (this.jwtService.loggedIn) {
      this.user = this.jwtService.userInfo;
      this.contact.name = this.user.name;
      this.contact.email = this.user.email;
      this.contact.address = this.user.address;
      this.contact.phone = this.user.phone;
    }
  }

  ngOnInit() {

  }

  submitForm() {
    console.log(this.contact);
  }

}
