import { Component, OnInit } from '@angular/core';
import { CartService } from 'src/app/services/cart.service';
import { JwtService } from 'src/app/services/jwt.service';
import { PayService } from 'src/app/services/pay.service';
import { Router } from '@angular/router';
declare var  stepperInit: any;
declare var LocalPicker: any;

@Component({
  selector: 'app-pay',
  templateUrl: './pay.component.html',
  styleUrls: ['./pay.component.sass']
})
export class PayComponent implements OnInit {

  public cart;
  public user;
  public shipFee;
  public shipUnit;
  public listOptions;
  public listPaymentMethod;
  public payMethod;
  public isSuccess;
  public note;
  public sending;
  public orderInfo;

  constructor(private route: Router, private payService: PayService, private cartService: CartService, private jwtService: JwtService) {
    this.user = {};
    if (jwtService.loggedIn) {
      this.user = this.jwtService.userInfo;
    }
    this.shipFee = 0;
    this.listOptions = [
      {id: 1, name: "Viettel Post"},
      {id: 2, name: "GHN"},
    ];
    this.listPaymentMethod = [
      {id: "COD", name: "Thanh toán khi nhận hàng", selected: true}
    ]
    this.shipUnit = this.listOptions[0].name;
    this.payMethod = this.listPaymentMethod[0].id;
    this.isSuccess = false;
    this.sending = true;
    this.orderInfo = {};
  }

  ngOnInit() {
    this.cart = this.cartService.getCart();
    stepperInit();
    // new LocalPicker({
    //   province: "ls_province",
    //   district: "ls_district",
    //   ward: "ls_ward"
    // });
  }

  selectShippingUnit(event) {
    this.shipUnit = event.target.value;
  }

  selectPaymentMethod(method) {
    this.payMethod = method.id;
  }

  getTotal() {
    return this.cartService.calcTotal();
  }

  buy() {
    let bill: any = {};
    bill.user = this.user;
    bill.products = [];
    this.cart.forEach(item => {
      bill.products.push({
        id: item.id,
        qty: item.qty
      })
    });
    bill.shippingUnit = this.shipUnit;
    bill.paymentMethod = this.payMethod;
    bill.note = this.note;
    this.payService.buy(bill).subscribe(res => {
      this.sending = false;
      if (res.status == "success") {
        this.isSuccess = true;
      }
      this.orderInfo = res;
    })
  }

  deleteCart() {
    this.cartService.removeCart();
    this.route.navigate([""]);
  }

}
