import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';
import * as jwt_decode from "jwt-decode";
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class JwtService {

  public userInfo;

  private server = "http://localhost:3000";

  constructor(private httpClient: HttpClient, @Inject(LOCAL_STORAGE) private storage: StorageService, private route: Router) {
    if (this.loggedIn) {
      this.userInfo = this.getDecodedAccessToken(this.storage.get('access_token'));
    }
  }

  login(email:string, password:string) {
    let path = "/auth/login";
    return this.httpClient.post<{accessToken:  string}>(this.server + path, {email, password})
    .pipe(
      tap(res => {
        this.storage.set('access_token', res.accessToken);
        this.userInfo = this.getDecodedAccessToken(res.accessToken);
        this.route.navigate(['']);
      })
    )
  }

  getDecodedAccessToken(token: string): any {
    try{
        return jwt_decode(token);
    }
    catch(Error){
        return null;
    }
  }

  register(userData) {
    let path = "/auth/register";
    return this.httpClient.post<{accessToken: string}>(this.server + path, userData)
    .pipe(
      tap(res => {
        this.storage.set('access_token', res.accessToken);
        this.userInfo = this.getDecodedAccessToken(res.accessToken);
        this.route.navigate(['']);
      })
    )
  }

  logout() {
    this.storage.remove('access_token');
    this.route.navigate(['']);
  }

  public get loggedIn(): boolean{
    return this.storage.has('access_token');
  }
}
