import { Component, OnInit } from '@angular/core';
import { BookService } from 'src/app/services/book.service';
import { Book } from 'src/models/books';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.sass']
})
export class ProductsComponent implements OnInit {
  public books;
  public authors;
  public authorFilter;

  constructor(private activeRoute: ActivatedRoute, private bookService: BookService) {
    this.books = [];
    this.authors = [];
    this.authorFilter = [];
  }


  ngOnInit() {
    
    this.activeRoute.params.subscribe(params => {
      console.log();
      this.bookService.getAllBooks().subscribe(books => {
        this.books = books.value.filter(book => book.category.indexOf(params['cate']) != -1);
        this.books.forEach(book => {
          this.authors = this.authors.concat(book.author);      
        });
      });
    });
  }

  filterByAuthor(author, event) {
    if (event.target.checked) {
      this.authorFilter.push(author.name);
    }
    else {
      this.authorFilter.splice(this.authorFilter.indexOf(author.name), 1);
    }
  }

  filter() {
    let tmpList = this.books;
    this.authorFilter.forEach(author => {
      tmpList.filter(book => book.author.name == author);
    });
    
  }
}
