import { Injectable, Inject } from '@angular/core';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';

const STORAGE_KEY = 'local_cart';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  private cart: any[] = [];

  constructor(@Inject(LOCAL_STORAGE) private storage: StorageService) {
    this.cart = this.storage.get(STORAGE_KEY) || [];
  }

  addToCard(book) {
    const existedItem = this.cart.find(item => item.id == book.id);
    if (existedItem) {
      existedItem.qty += book.qty;
    }
    else {
      this.cart.push(book);
    }
    this.storage.set(STORAGE_KEY, this.cart);
  }

  getCart() {
    this.cart = this.storage.get(STORAGE_KEY);
    return this.cart;
  }

  calcTotal() {
    let total = 0;
    for (let item of this.cart) {
     total += item.finalPrice * item.qty;
    }
    return total;
  }

  storeOnLocalStorage(book) {
    let currentCart = this.storage.get(STORAGE_KEY) || []
    this.storage.set(STORAGE_KEY, currentCart.push(book));
    this.cart = currentCart;
  }

  count() {
    let sum = 0;
    this.cart.map(item => sum += item.qty);
    return sum;
  }

  removeCart() {
    this.cart = [];
    this.storage.set(STORAGE_KEY, []);
  }

}
