import { Component, OnInit } from '@angular/core';
import { Category } from 'src/models/categories';
import { CategoryService } from 'src/app/services/category.service';
import { BannerService } from 'src/app/services/banner.service';
import { SaleBanner } from 'src/models/salebanners';
import { BookService } from 'src/app/services/book.service';
import { CartService } from 'src/app/services/cart.service';
declare var countDownSale: any;


@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.sass']
})
export class HomepageComponent implements OnInit {
  categories: any[] = [];
  banners: SaleBanner[];
  books: any[];

  constructor(
    private categoryService: CategoryService,
    private saleBannerService: BannerService,
    private bookService: BookService,
    private cartService: CartService
  ) { 
    this.books = [];
  }

  getCategory() {
    // this.categories = this.categoryService.getCategory();
    this.categoryService.getCategory().subscribe((updatedCategory) => {
      this.categories = updatedCategory.categories;
    });
  }

  getSaleBanner() {
    // this.categories = this.categoryService.getCategory();
    this.saleBannerService.getSaleBanners().subscribe((updatedCategory) => {
      this.banners = updatedCategory.banners;
    });
  }

  getAllBooks() {
    this.bookService.getAllBooks().subscribe(books => {
      this.books = books.value || [];
    })
  }

  ngOnInit() {
    this.getCategory();
    this.getSaleBanner();
    this.getAllBooks();
  }

  countdown(book) {
    countDownSale(book.endSale, "a" + book.id);
  }

  getSaleBooks() {
    let saleBooks = this.books.filter(book => book.isSell)
    return saleBooks;
  }

  addToCart(saleBook) {
    this.cartService.addToCard({...saleBook, ...{qty: 1}});
  }

  getNewProducts() {
    return this.books.filter(book => {
      if (!book.tags || book.tags.length == 0) return false;
      
      console.log(book.tags.indexOf("new") != -1);
      return book.tags.indexOf("new") != -1;
    });
  }

  getBestSellerProducts() {
    return this.books.filter(book => {
      if (!book.tags || book.tags.length == 0) return false;
      return book.tags.indexOf("bestseller") != -1;
    });
  }

}
